// __tests__/screens/Dashboard/Dashboard.integration.test.js
import React from 'react';
import { render, fireEvent, waitFor } from '@testing-library/react-native';
import Dashboard from '../../../src/screens/Dashboard';
import { ThemeContext } from '../../../src/contexts/ThemeContext';

// Mock des dépendances
jest.mock('expo-linear-gradient', () => ({
  LinearGradient: ({ children }) => children
}));

jest.mock('expo-router', () => ({
  router: { push: jest.fn() }
}));

// Mock des contextes
const mockUseProgress = jest.fn();
const mockUseCurrentLevel = jest.fn();

jest.mock('../../../src/contexts/ProgressContext', () => ({
  useProgress: mockUseProgress,
}));

jest.mock('../../../src/contexts/CurrentLevelContext', () => ({
  useCurrentLevel: mockUseCurrentLevel,
}));

// Mock des hooks Dashboard
const mockUseDashboardLevel = jest.fn();
const mockUseDashboardState = jest.fn();
const mockUseLastActivity = jest.fn();
const mockUseRealTimeProgress = jest.fn();

jest.mock('../../../src/screens/Dashboard/hooks/useDashboardLevel', () => ({
  useDashboardLevel: mockUseDashboardLevel,
}));

jest.mock('../../../src/screens/Dashboard/hooks/useDashboardState', () => ({
  useDashboardState: mockUseDashboardState,
}));

jest.mock('../../../src/hooks/useLastActivity', () => ({
  __esModule: true,
  default: mockUseLastActivity,
}));

jest.mock('../../../src/hooks/useRealTimeProgress', () => ({
  __esModule: true,
  default: mockUseRealTimeProgress,
}));

// Mock des composants (simples)
jest.mock('../../../src/screens/Dashboard/components/ModernHeader', () => 
  ({ level }) => `<ModernHeader level="${level}" />`
);

jest.mock('../../../src/screens/Dashboard/components/HeroContinueSection', () => 
  ({ onPress, lastActivity }) => 
    `<HeroContinueSection><button onclick={() => onPress(${lastActivity ? 'lastActivity' : '"levelSelection"'})}>Continue</button></HeroContinueSection>`
);

jest.mock('../../../src/screens/Dashboard/components/QuickActions', () => 
  () => '<QuickActions />'
);

jest.mock('../../../src/screens/Dashboard/components/SimpleMetrics', () => 
  () => '<SimpleMetrics />'
);

jest.mock('../../../src/screens/Dashboard/components/LearningProgress', () => 
  ({ onSelectLevel, onChangeLevelVisual }) => 
    `<LearningProgress><button onclick={() => onSelectLevel('2')}>Level 2</button><button onclick={() => onChangeLevelVisual('3')}>Change Visual</button></LearningProgress>`
);

jest.mock('../../../src/screens/VocabularyRevision/RevisionOrchestrator', () => 
  () => '<RevisionOrchestrator />'
);

jest.mock('../../../src/components/layout/Container', () => 
  ({ children }) => children
);

// Mock des constantes
jest.mock('../../../src/utils/constants', () => ({
  LANGUAGE_LEVELS: {
    '1': { title: 'Débutant', color: '#10B981' },
    '2': { title: 'Élémentaire', color: '#3B82F6' },
    '3': { title: 'Intermédiaire', color: '#8B5CF6' },
  },
  EXERCISES: {
    vocabulary: { id: 'vocabulary', route: '/tabs/vocabulary' },
    grammar: { id: 'grammar', route: '/tabs/grammar' }
  },
  CONTAINER_SAFE_EDGES: { NO_BOTTOM: [] }
}));

describe('Dashboard - Intégration', () => {
  const defaultMocks = {
    progress: { isLoading: false, progress: {} },
    currentLevel: { setCurrentLevel: jest.fn() },
    dashboardLevel: { currentLevel: '1', handleChangeActiveLevel: jest.fn(), levelColor: '#10B981' },
    dashboardState: { refreshing: false, onRefresh: jest.fn() },
    lastActivity: { lastActivity: null, isLoading: false, reload: jest.fn() },
    realTimeProgress: { getLevelProgress: jest.fn(() => 25), refresh: jest.fn() }
  };

  beforeEach(() => {
    jest.clearAllMocks();
    
    // Setup mocks par défaut
    mockUseProgress.mockReturnValue(defaultMocks.progress);
    mockUseCurrentLevel.mockReturnValue(defaultMocks.currentLevel);
    mockUseDashboardLevel.mockReturnValue(defaultMocks.dashboardLevel);
    mockUseDashboardState.mockReturnValue(defaultMocks.dashboardState);
    mockUseLastActivity.mockReturnValue(defaultMocks.lastActivity);
    mockUseRealTimeProgress.mockReturnValue(defaultMocks.realTimeProgress);
  });

  const renderDashboard = () => {
    const themeContext = {
      colors: {
        background: '#F8FAFC',
        primary: '#3B82F6',
        surface: '#FFFFFF',
        text: '#1F2937'
      }
    };

    return render(
      <ThemeContext.Provider value={themeContext}>
        <Dashboard />
      </ThemeContext.Provider>
    );
  };

  it('devrait rendre le dashboard correctement', () => {
    const { container } = renderDashboard();
    expect(container.firstChild).toBeTruthy();
  });

  it('devrait afficher le loading quand isLoading est true', () => {
    mockUseProgress.mockReturnValue({ isLoading: true, progress: {} });
    
    const { getByText } = renderDashboard();
    expect(getByText('Chargement du tableau de bord...')).toBeTruthy();
  });

  it('devrait naviguer vers levelSelection sans lastActivity', async () => {
    const { router } = require('expo-router');
    
    mockUseLastActivity.mockReturnValue({
      lastActivity: null,
      isLoading: false,
      reload: jest.fn()
    });

    renderDashboard();
    
    // Simuler le clic sur Continue (qui devrait aller vers levelSelection)
    const handleContinue = mockUseDashboardLevel.mock.calls[0]?.[0]?.onContinue || 
                          (() => router.push("/(tabs)/levelSelection"));
    
    handleContinue("levelSelection");
    
    expect(router.push).toHaveBeenCalledWith("/(tabs)/levelSelection");
  });

  it('devrait naviguer vers vocabulary avec lastActivity', async () => {
    const { router } = require('expo-router');
    
    const lastActivity = {
      type: 'vocabulary',
      level: '2',
      title: 'Test Vocab'
    };

    mockUseLastActivity.mockReturnValue({
      lastActivity,
      isLoading: false,
      reload: jest.fn()
    });

    renderDashboard();
    
    // Simuler la navigation avec lastActivity
    const handleContinue = () => {
      router.push({
        pathname: '/tabs/vocabulary',
        params: { level: '2' }
      });
    };
    
    handleContinue();
    
    expect(router.push).toHaveBeenCalledWith({
      pathname: '/tabs/vocabulary',
      params: { level: '2' }
    });
  });

  it('devrait changer de niveau et synchroniser le contexte', () => {
    const mockSetCurrentLevel = jest.fn();
    const mockHandleChangeActiveLevel = jest.fn();
    
    mockUseCurrentLevel.mockReturnValue({
      setCurrentLevel: mockSetCurrentLevel
    });
    
    mockUseDashboardLevel.mockReturnValue({
      currentLevel: '1',
      handleChangeActiveLevel: mockHandleChangeActiveLevel,
      levelColor: '#10B981'
    });

    renderDashboard();
    
    // Simuler le changement de niveau visuel
    const handleChangeLevelVisual = (levelId) => {
      mockHandleChangeActiveLevel(levelId);
      mockSetCurrentLevel(levelId);
    };
    
    handleChangeLevelVisual('3');
    
    expect(mockHandleChangeActiveLevel).toHaveBeenCalledWith('3');
    expect(mockSetCurrentLevel).toHaveBeenCalledWith('3');
  });

  it('devrait naviguer vers exerciseSelection lors de la sélection de niveau', () => {
    const { router } = require('expo-router');
    const mockSetCurrentLevel = jest.fn();
    
    mockUseCurrentLevel.mockReturnValue({
      setCurrentLevel: mockSetCurrentLevel
    });

    renderDashboard();
    
    // Simuler la sélection de niveau
    const handleLevelSelect = (level) => {
      mockSetCurrentLevel(level);
      router.push(`/tabs/exerciseSelection?level=${level}`);
    };
    
    handleLevelSelect('2');
    
    expect(mockSetCurrentLevel).toHaveBeenCalledWith('2');
    expect(router.push).toHaveBeenCalledWith('/tabs/exerciseSelection?level=2');
  });

  it('devrait calculer la progression correctement', () => {
    const mockGetLevelProgress = jest.fn();
    mockGetLevelProgress.mockReturnValue(42);
    
    mockUseRealTimeProgress.mockReturnValue({
      getLevelProgress: mockGetLevelProgress,
      refresh: jest.fn()
    });

    renderDashboard();
    
    expect(mockGetLevelProgress).toHaveBeenCalledWith('1'); // niveau par défaut
  });
});